<script>
  import { range, durationUnitRegex } from "./utils";
  export let size = 60;
  export let color = "#FF3E00";
  export let unit = "px";

  export let duration = "2.1s";

  let durationUnit = duration.match(durationUnitRegex)[0];
  let durationNum = duration.replace(durationUnitRegex, "");

</script>
<style>.wrapper{position:relative}.circle,.wrapper{width:var(--size);height:var(--size)}.circle{position:absolute;background-color:var(--color);border-radius:100%;opacity:.6;top:0;left:0;-webkit-animation-fill-mode:both;animation-fill-mode:both;-webkit-animation-name:bounce!important;animation-name:bounce!important}@-webkit-keyframes bounce{0%,to{-webkit-transform:scale(0);transform:scale(0)}50%{-webkit-transform:scale(1);transform:scale(1)}}@keyframes bounce{0%,to{-webkit-transform:scale(0);transform:scale(0)}50%{-webkit-transform:scale(1);transform:scale(1)}}</style>
<div class="wrapper" style="--size: {size}{unit}; --color: {color}">
  {#each range(2, 1) as version}
  <div
    class="circle"
    style="animation: {duration} {version === 1 ? `${(durationNum-0.1)/2}${durationUnit}` : `0s`} infinite ease-in-out"
  ></div>
  {/each}
</div>
