<script>
  import { calculateRgba } from "./utils";
  export let size = 60;
  export let color = "#FF3E00";
  export let unit = "px";
  export let duration = "1.3s";
  $: rgba = calculateRgba(color, 0.6);
</script>

<style>.wrapper{height:var(--size);width:var(--size);position:relative;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.wrapper *{line-height:0;-webkit-box-sizing:border-box;box-sizing:border-box}.spinner-inner{height:var(--size);width:var(--size);-webkit-transform:scale(calc(var(--size)/70));-ms-transform:scale(calc(var(--size)/70));transform:scale(calc(var(--size)/70))}.mask{border-radius:2px;overflow:hidden}.mask,.plane{position:absolute;-webkit-perspective:1000;perspective:1000;-webkit-backface-visibility:hidden;backface-visibility:hidden}.plane{background:var(--color);width:400%;height:100%;z-index:100}#top .plane{z-index:2000;-webkit-animation:trans1 var(--duration) ease-in infinite 0s backwards;animation:trans1 var(--duration) ease-in infinite 0s backwards}#middle .plane{-webkit-transform:translateZ(0);transform:translateZ(0);background:var(--rgba);-webkit-animation:trans2 var(--duration) linear infinite calc(var(--duration)/4) backwards;animation:trans2 var(--duration) linear infinite calc(var(--duration)/4) backwards}#bottom .plane{z-index:2000;-webkit-animation:trans3 var(--duration) ease-out infinite calc(var(--duration)/2) backwards;animation:trans3 var(--duration) ease-out infinite calc(var(--duration)/2) backwards}#top{width:53px;top:5px;-webkit-transform:skew(-15deg,0);-ms-transform:skew(-15deg,0);transform:skew(-15deg,0);z-index:100}#middle,#top{height:20px;left:20px}#middle{width:33px;top:21px;-webkit-transform:skew(-15deg,40deg);-ms-transform:skew(-15deg,40deg);transform:skew(-15deg,40deg)}#bottom{width:53px;height:20px;top:35px;-webkit-transform:skew(-15deg,0);-ms-transform:skew(-15deg,0);transform:skew(-15deg,0)}@-webkit-keyframes trans1{0%{-webkit-transform:translate3d(53px,0,0);transform:translate3d(53px,0,0)}to{-webkit-transform:translate3d(-250px,0,0);transform:translate3d(-250px,0,0)}}@keyframes trans1{0%{-webkit-transform:translate3d(53px,0,0);transform:translate3d(53px,0,0)}to{-webkit-transform:translate3d(-250px,0,0);transform:translate3d(-250px,0,0)}}@-webkit-keyframes trans2{0%{-webkit-transform:translate3d(-160px,0,0);transform:translate3d(-160px,0,0)}to{-webkit-transform:translate3d(53px,0,0);transform:translate3d(53px,0,0)}}@keyframes trans2{0%{-webkit-transform:translate3d(-160px,0,0);transform:translate3d(-160px,0,0)}to{-webkit-transform:translate3d(53px,0,0);transform:translate3d(53px,0,0)}}@-webkit-keyframes trans3{0%{-webkit-transform:translate3d(53px,0,0);transform:translate3d(53px,0,0)}to{-webkit-transform:translate3d(-220px,0,0);transform:translate3d(-220px,0,0)}}@keyframes trans3{0%{-webkit-transform:translate3d(53px,0,0);transform:translate3d(53px,0,0)}to{-webkit-transform:translate3d(-220px,0,0);transform:translate3d(-220px,0,0)}}</style>

<div
  class="wrapper"
  style="--size: {size}{unit}; --color: {color}; --rgba: {rgba}; --duration: {duration};"
>
  <div class="spinner-inner">
    <div id="top" class="mask">
      <div class="plane"></div>
    </div>
    <div id="middle" class="mask">
      <div class="plane"></div>
    </div>
    <div id="bottom" class="mask">
      <div class="plane"></div>
    </div>
  </div>
</div>
