<script>
  export let size = 60;
  export let color = "#FF3E00";
  export let unit = "px";
  export let duration = "3s";
</script>

<style>.wrapper{width:var(--size);height:calc(var(--size)/2);overflow:hidden}.rainbow{width:var(--size);height:var(--size);border-left-color:rgba(0,0,0,0);border-bottom-color:rgba(0,0,0,0);border-top-color:var(--color);border-right-color:var(--color);-webkit-box-sizing:border-box;box-sizing:border-box;-webkit-transform:rotate(-200deg);-ms-transform:rotate(-200deg);transform:rotate(-200deg);border-radius:50%;border-style:solid;-webkit-animation:var(--duration) ease-in-out 0s infinite normal none running rotate;animation:var(--duration) ease-in-out 0s infinite normal none running rotate}@-webkit-keyframes rotate{0%{border-width:10px}25%{border-width:3px}50%{-webkit-transform:rotate(115deg);transform:rotate(115deg);border-width:10px}75%{border-width:3px}to{border-width:10px}}@keyframes rotate{0%{border-width:10px}25%{border-width:3px}50%{-webkit-transform:rotate(115deg);transform:rotate(115deg);border-width:10px}75%{border-width:3px}to{border-width:10px}}</style>

<div class="wrapper" style="--size: {size}{unit}; --color: {color}; --duration: {duration};">
  <div class="rainbow"></div>
</div>
