<script>
  import { range } from "./utils";
  export let size = 60;
  export let color = "#FF3E00";
  export let unit = "px";
  export let duration = "2s";
</script>

<style>.wrapper{position:relative}.border,.wrapper{width:var(--size);height:var(--size)}.border{position:absolute;top:0;left:0;opacity:.4;-webkit-perspective:800px;perspective:800px;border:6px solid var(--color);-o-border-image:initial;border-image:initial;border-radius:100%}.border.\31{-webkit-animation:var(--duration) linear 0s infinite normal none running ringOne;animation:var(--duration) linear 0s infinite normal none running ringOne}.border.\32{-webkit-animation:var(--duration) linear 0s infinite normal none running ringTwo;animation:var(--duration) linear 0s infinite normal none running ringTwo}@-webkit-keyframes ringOne{0%{-webkit-transform:rotateX(0deg) rotateY(0deg) rotate(0deg);transform:rotateX(0deg) rotateY(0deg) rotate(0deg)}to{-webkit-transform:rotateX(1turn) rotateY(180deg) rotate(1turn);transform:rotateX(1turn) rotateY(180deg) rotate(1turn)}}@keyframes ringOne{0%{-webkit-transform:rotateX(0deg) rotateY(0deg) rotate(0deg);transform:rotateX(0deg) rotateY(0deg) rotate(0deg)}to{-webkit-transform:rotateX(1turn) rotateY(180deg) rotate(1turn);transform:rotateX(1turn) rotateY(180deg) rotate(1turn)}}@-webkit-keyframes ringTwo{0%{-webkit-transform:rotateX(0deg) rotateY(0deg) rotate(0deg);transform:rotateX(0deg) rotateY(0deg) rotate(0deg)}to{-webkit-transform:rotateX(180deg) rotateY(1turn) rotate(1turn);transform:rotateX(180deg) rotateY(1turn) rotate(1turn)}}@keyframes ringTwo{0%{-webkit-transform:rotateX(0deg) rotateY(0deg) rotate(0deg);transform:rotateX(0deg) rotateY(0deg) rotate(0deg)}to{-webkit-transform:rotateX(180deg) rotateY(1turn) rotate(1turn);transform:rotateX(180deg) rotateY(1turn) rotate(1turn)}}</style>

<div class="wrapper" style="--size: {size}{unit}; --color: {color}; --duration: {duration};">
  {#each range(2, 1) as version}
  <div class="border {version}"></div>
  {/each}
</div>
