<script>
  export let size = 60;
  export let color = "#FF3E00";
  export let unit = "px";
  export let stroke = parseInt(size / 12) + unit;
  export let duration = "4s";
</script>
<style>.wrapper{-webkit-transform:scale(calc(var(--floatSize)/75));-ms-transform:scale(calc(var(--floatSize)/75));transform:scale(calc(var(--floatSize)/75));display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.line,.wrapper{width:var(--size);height:var(--stroke)}.line{background:var(--color);border-radius:var(--stroke);-webkit-transform-origin:center center;-ms-transform-origin:center center;transform-origin:center center;-webkit-animation:spineLine var(--duration) ease infinite;animation:spineLine var(--duration) ease infinite}@-webkit-keyframes spineLine{0%{-webkit-transform:rotate(-20deg);transform:rotate(-20deg);height:5px;width:75px}5%{height:5px;width:75px}30%{-webkit-transform:rotate(380deg);transform:rotate(380deg);height:5px;width:75px}40%{-webkit-transform:rotate(1turn);transform:rotate(1turn);height:5px;width:75px}55%{-webkit-transform:rotate(0deg);transform:rotate(0deg);height:5px;width:5px}65%{-webkit-transform:rotate(0deg);transform:rotate(0deg);height:5px;width:85px}68%{-webkit-transform:rotate(0deg);transform:rotate(0deg);height:5px}75%{-webkit-transform:rotate(0deg);transform:rotate(0deg);height:5px;width:1px}78%{height:5px;width:5px}90%{height:5px;width:75px;-webkit-transform:rotate(0deg);transform:rotate(0deg)}99%,to{height:5px;width:75px;-webkit-transform:rotate(-20deg);transform:rotate(-20deg)}}@keyframes spineLine{0%{-webkit-transform:rotate(-20deg);transform:rotate(-20deg);height:5px;width:75px}5%{height:5px;width:75px}30%{-webkit-transform:rotate(380deg);transform:rotate(380deg);height:5px;width:75px}40%{-webkit-transform:rotate(1turn);transform:rotate(1turn);height:5px;width:75px}55%{-webkit-transform:rotate(0deg);transform:rotate(0deg);height:5px;width:5px}65%{-webkit-transform:rotate(0deg);transform:rotate(0deg);height:5px;width:85px}68%{-webkit-transform:rotate(0deg);transform:rotate(0deg);height:5px}75%{-webkit-transform:rotate(0deg);transform:rotate(0deg);height:5px;width:1px}78%{height:5px;width:5px}90%{height:5px;width:75px;-webkit-transform:rotate(0deg);transform:rotate(0deg)}99%,to{height:5px;width:75px;-webkit-transform:rotate(-20deg);transform:rotate(-20deg)}}</style>
<div
  class="wrapper"
  style="--size: {size}{unit}; --color: {color}; --stroke: {stroke}; --floatSize: {size}; --duration: {duration}"
>
  <div class="line"></div>
</div>
