<script>
  import { calculateRgba, range } from "./utils";
  export let size = 60;
  export let color = "#FF3E00";
  export let unit = "px";
  export let duration = "2.1s";
  $: rgba = calculateRgba(color, 0.2);
</script>

<style>.wrapper{width:calc(var(--size)*2);background-color:var(--rgba);position:relative;overflow:hidden;background-clip:padding-box}.lines,.wrapper{height:calc(var(--size)/15)}.lines{background-color:var(--color)}.small-lines{position:absolute;overflow:hidden;background-clip:padding-box;display:block;border-radius:2px;will-change:left,right;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.small-lines.\31{-webkit-animation:var(--duration) cubic-bezier(.65,.815,.735,.395) 0s infinite normal none running long;animation:var(--duration) cubic-bezier(.65,.815,.735,.395) 0s infinite normal none running long}.small-lines.\32{-webkit-animation:var(--duration) cubic-bezier(.165,.84,.44,1) calc(var(--duration)/2 + .05) infinite normal none running short;animation:var(--duration) cubic-bezier(.165,.84,.44,1) calc(var(--duration)/2 + .05) infinite normal none running short}@-webkit-keyframes long{0%{left:-35%;right:100%}60%{left:100%;right:-90%}to{left:100%;right:-90%}}@keyframes long{0%{left:-35%;right:100%}60%{left:100%;right:-90%}to{left:100%;right:-90%}}@-webkit-keyframes short{0%{left:-200%;right:100%}60%{left:107%;right:-8%}to{left:107%;right:-8%}}@keyframes short{0%{left:-200%;right:100%}60%{left:107%;right:-8%}to{left:107%;right:-8%}}</style>

<div class="wrapper" style="--size: {size}{unit}; --rgba:{rgba}">
  {#each range(2, 1) as version}
  <div class="lines small-lines {version}" style="--color: {color}; --duration: {duration};"></div>
  {/each}
</div>
