<script>
  export let size = 60;
  export let color = "#FF3E00";
  export let unit = "px";
  export let duration = "0.75s";
</script>
<style>.circle{height:var(--size);width:var(--size);border:calc(var(--size)/15) solid var(--color);border-right:calc(var(--size)/15) solid rgba(0,0,0,0);-o-border-image:initial;border-image:initial;border-radius:50%;-webkit-animation:var(--duration) linear 0s infinite normal none running rotate;animation:var(--duration) linear 0s infinite normal none running rotate}@-webkit-keyframes rotate{0%{-webkit-transform:rotate(0);transform:rotate(0)}to{-webkit-transform:rotate(1turn);transform:rotate(1turn)}}@keyframes rotate{0%{-webkit-transform:rotate(0);transform:rotate(0)}to{-webkit-transform:rotate(1turn);transform:rotate(1turn)}}</style>
<div class="circle" style="--size: {size}{unit}; --color: {color}; --duration: {duration}"></div>
