<script>
  import { range, durationUnitRegex } from "./utils";
  export let size = 60;
  export let color = "#FF3E00";
  export let unit = "px";
  export let duration = "1.25s";

  let durationUnit = duration.match(durationUnitRegex)[0];
  let durationNum = duration.replace(durationUnitRegex, "");
</script>
<style>.wrapper{position:relative;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;width:calc(var(--size)*2.5);height:var(--size);overflow:hidden}.bar{position:absolute;top:calc(var(--size)/10);width:calc(var(--size)/5);height:calc(var(--size)/10);margin-top:calc(var(--size) - var(--size)/10);-webkit-transform:skewY(0deg);-ms-transform:skewY(0deg);transform:skewY(0deg);background-color:var(--color);-webkit-animation:motion var(--duration) ease-in-out infinite;animation:motion var(--duration) ease-in-out infinite}@-webkit-keyframes motion{25%{-webkit-transform:skewY(25deg);transform:skewY(25deg)}50%{height:100%;margin-top:0}75%{-webkit-transform:skewY(-25deg);transform:skewY(-25deg)}}@keyframes motion{25%{-webkit-transform:skewY(25deg);transform:skewY(25deg)}50%{height:100%;margin-top:0}75%{-webkit-transform:skewY(-25deg);transform:skewY(-25deg)}}</style>
<div class="wrapper" style="--size: {size}{unit}; --color: {color}; --duration: {duration};">
  {#each range(10, 0) as version}
  <div
    class="bar"
    style="left: {version * (size / 5 + (size / 15 - size / 100))+unit}; animation-delay: {version * (durationNum/8.3)}{durationUnit};"
  ></div>
  {/each}
</div>
