<script>
  import { range, durationUnitRegex } from "./utils";
  export let size = 60;
  export let color = "#FF3E00";
  export let unit = "px";
  export let duration = "1s";

  let durationUnit = duration.match(durationUnitRegex)[0];
  let durationNum = duration.replace(durationUnitRegex, "");
</script>

<style>.circle,.wrapper{width:var(--size);height:var(--size)}.circle{border-radius:100%;-webkit-animation-fill-mode:both;animation-fill-mode:both;position:absolute;opacity:0;background-color:var(--color);-webkit-animation:bounce var(--duration) linear infinite;animation:bounce var(--duration) linear infinite}@-webkit-keyframes bounce{0%{opacity:0;-webkit-transform:scale(0);transform:scale(0)}5%{opacity:1}to{opacity:0;-webkit-transform:scale(1);transform:scale(1)}}@keyframes bounce{0%{opacity:0;-webkit-transform:scale(0);transform:scale(0)}5%{opacity:1}to{opacity:0;-webkit-transform:scale(1);transform:scale(1)}}</style>

<div class="wrapper" style="--size: {size}{unit}; --color: {color}; --duration: {duration};">
  {#each range(3, 1) as version}
    <div
      class="circle"
      style="animation-delay: {(durationNum/3)*(version-1) + durationUnit};" />
  {/each}
</div>
