<script>
  export let size = 60;
  export let color = "#FF3E00";
  export let unit = "px";
  export let duration = "0.6s";
  let moonSize = size / 7;
  let top = size / 2 - moonSize / 2;
</script>

<style>.wrapper{height:var(--size);width:var(--size);position:relative}.circle-one,.wrapper{border-radius:100%;-webkit-animation:moonStretchDelay var(--duration) 0s infinite linear;animation:moonStretchDelay var(--duration) 0s infinite linear;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.circle-one{top:var(--moonSize);background-color:var(--color);width:calc(var(--size)/7);height:calc(var(--size)/7);opacity:.8;position:absolute}.circle-two{opacity:.1;border:calc(var(--size)/7) solid var(--color);height:var(--size);width:var(--size);border-radius:100%;-webkit-box-sizing:border-box;box-sizing:border-box}@-webkit-keyframes moonStretchDelay{to{-webkit-transform:rotate(1turn);transform:rotate(1turn)}}@keyframes moonStretchDelay{to{-webkit-transform:rotate(1turn);transform:rotate(1turn)}}</style>

<div
  class="wrapper"
  style="--size: {size}{unit}; --color: {color}; --moonSize: {top}{unit}; --duration: {duration};"
>
  <div class="circle-one"></div>
  <div class="circle-two"></div>
</div>
