<script>
  import { range, durationUnitRegex } from "./utils";

  export let size = 60;
  export let color = "#FF3E00";
  export let duration = "1.2s";
  export let unit = "px";

  let durationUnit = duration.match(durationUnitRegex)[0];
  let durationNum = duration.replace(durationUnitRegex, "");
</script>

<style>.wrapper{height:var(--size);width:var(--size);text-align:center;font-size:10px}.rect,.wrapper{display:inline-block}.rect{height:100%;width:10%;margin-right:4px;background-color:var(--color);-webkit-animation:stretch var(--duration) ease-in-out infinite;animation:stretch var(--duration) ease-in-out infinite}@-webkit-keyframes stretch{0%,40%,to{-webkit-transform:scaleY(.4);transform:scaleY(.4)}20%{-webkit-transform:scaleY(1);transform:scaleY(1)}}@keyframes stretch{0%,40%,to{-webkit-transform:scaleY(.4);transform:scaleY(.4)}20%{-webkit-transform:scaleY(1);transform:scaleY(1)}}</style>

<div
  class="wrapper"
  style="--size: {size}{unit}; --color: {color}; --duration: {duration}"
>
  {#each range(5, 1) as version}
  <div
    class="rect"
    style="animation-delay: {(version-1)*(durationNum/12)}{durationUnit}"
  ></div>
  {/each}
</div>
