<script>
  import { durationUnitRegex, range } from "./utils";
  export let size = 60;
  export let color = "#FF3E00";
  export let unit = "px";

  export let duration = "2s";

  let durationUnit = duration.match(durationUnitRegex)[0];
  let durationNum = duration.replace(durationUnitRegex, "");
</script>

<style>.wrapper{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.spinner,.wrapper{height:var(--size);width:var(--size)}.spinner{-webkit-animation:rotate var(--duration) infinite linear;animation:rotate var(--duration) infinite linear}.dot{width:60%;height:60%;display:inline-block;position:absolute;top:0;background-color:var(--color);border-radius:100%;-webkit-animation:bounce var(--duration) infinite ease-in-out;animation:bounce var(--duration) infinite ease-in-out}@-webkit-keyframes rotate{to{-webkit-transform:rotate(1turn);transform:rotate(1turn)}}@keyframes rotate{to{-webkit-transform:rotate(1turn);transform:rotate(1turn)}}@-webkit-keyframes bounce{0%,to{-webkit-transform:scale(0);transform:scale(0)}50%{-webkit-transform:scale(1);transform:scale(1)}}@keyframes bounce{0%,to{-webkit-transform:scale(0);transform:scale(0)}50%{-webkit-transform:scale(1);transform:scale(1)}}</style>

<div class="wrapper" style="--size: {size}{unit}; --color: {color}; --duration: {duration};">
  <div class="spinner">
    {#each range(2, 0) as version}
      <div
        class="dot"
        style="animation-delay: {version === 1 ? `${durationNum/2}${durationUnit}` : '0s'}; bottom: {version === 1 ? '0' : ''}; top: {version === 1 ? 'auto' : ''};" />
    {/each}
  </div>
</div>
