<script>
  export let size = 60;
  export let color = "#FF3E00";
  export let unit = "px";
  export let duration = "1.25s";
</script>
<style>.wrapper{width:calc(var(--size)*1.3);height:calc(var(--size)*1.3);display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.firework{border:calc(var(--size)/10) dotted var(--color);width:var(--size);height:var(--size);border-radius:50%;-webkit-animation:fire var(--duration) cubic-bezier(.165,.84,.44,1) infinite;animation:fire var(--duration) cubic-bezier(.165,.84,.44,1) infinite}@-webkit-keyframes fire{0%{opacity:1;-webkit-transform:scale(.1);transform:scale(.1)}25%{opacity:.85}to{-webkit-transform:scale(1);transform:scale(1);opacity:0}}@keyframes fire{0%{opacity:1;-webkit-transform:scale(.1);transform:scale(.1)}25%{opacity:.85}to{-webkit-transform:scale(1);transform:scale(1);opacity:0}}</style>
<div class="wrapper" style="--size: {size}{unit}; --color: {color}; --duration: {duration};">
  <div class="firework"></div>
</div>
