<script>
  export let size = 60;
  export let unit = "px";
  export let colorOuter = "#FF3E00";
  export let colorCenter = "#40B3FF";
  export let colorInner = "#676778";
  export let durationMultiplier = 1;
  export let durationOuter = `${durationMultiplier*2}s`;
  export let durationInner = `${durationMultiplier*1.5}s`;
  export let durationCenter = `${durationMultiplier*3}s`;
</script>

<style>.circle{width:var(--size);height:var(--size);-webkit-box-sizing:border-box;box-sizing:border-box;position:relative;border:3px solid rgba(0,0,0,0);border-top:3px solid var(--colorOuter);border-radius:50%;-webkit-animation:circleSpin var(--durationOuter) linear infinite;animation:circleSpin var(--durationOuter) linear infinite}.circle:after,.circle:before{content:"";-webkit-box-sizing:border-box;box-sizing:border-box;position:absolute;border:3px solid rgba(0,0,0,0);border-radius:50%}.circle:after{border-top-color:var(--colorInner);top:9px;left:9px;right:9px;bottom:9px;-webkit-animation:circleSpin var(--durationInner) linear infinite;animation:circleSpin var(--durationInner) linear infinite}.circle:before{border-top-color:var(--colorCenter);top:3px;left:3px;right:3px;bottom:3px;-webkit-animation:circleSpin var(--durationCenter) linear infinite;animation:circleSpin var(--durationCenter) linear infinite}@-webkit-keyframes circleSpin{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(1turn);transform:rotate(1turn)}}@keyframes circleSpin{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(1turn);transform:rotate(1turn)}}</style>

<div
  class="circle"
  style="--size: {size}{unit}; --colorInner: {colorInner}; --colorCenter: {colorCenter}; --colorOuter: {colorOuter}; --durationInner: {durationInner}; --durationCenter: {durationCenter}; --durationOuter: {durationOuter};"
></div>
