<script>
  export let size = 60;
  export let color = "#FF3E00";
  export let unit = "px";

  export let duration = "1.5s";
</script>

<style>span{width:var(--size);position:relative}div,span{height:calc(var(--size)/4)}div{width:calc(var(--size)/4);position:absolute;left:0;top:0;border-radius:2px;background:var(--color);-webkit-transform:translateX(-50%) rotate(45deg) scale(0);-ms-transform:translateX(-50%) rotate(45deg) scale(0);transform:translateX(-50%) rotate(45deg) scale(0);-webkit-animation:diamonds var(--duration) linear infinite;animation:diamonds var(--duration) linear infinite}div:first-child{-webkit-animation-delay:calc(var(--duration)*2/3*-1);animation-delay:calc(var(--duration)*2/3*-1)}div:nth-child(2){-webkit-animation-delay:calc(var(--duration)*2/3*-2);animation-delay:calc(var(--duration)*2/3*-2)}div:nth-child(3){-webkit-animation-delay:calc(var(--duration)*2/3*-3);animation-delay:calc(var(--duration)*2/3*-3)}@-webkit-keyframes diamonds{50%{left:50%;-webkit-transform:translateX(-50%) rotate(45deg) scale(1);transform:translateX(-50%) rotate(45deg) scale(1)}to{left:100%;-webkit-transform:translateX(-50%) rotate(45deg) scale(0);transform:translateX(-50%) rotate(45deg) scale(0)}}@keyframes diamonds{50%{left:50%;-webkit-transform:translateX(-50%) rotate(45deg) scale(1);transform:translateX(-50%) rotate(45deg) scale(1)}to{left:100%;-webkit-transform:translateX(-50%) rotate(45deg) scale(0);transform:translateX(-50%) rotate(45deg) scale(0)}}</style>

<span style="--size: {size}{unit}; --color:{color}; --duration: {duration};">
  <div />
  <div />
  <div />
</span>
